@media screen and (min-width: 0px) {
    .pooldetail_container{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 90%;
        max-width: 600px;
        margin: 0 auto;
        margin-top: 15px;
        color: #ffffff;
    }

    .pooldetail_content{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        color: #ffffff;
        width: 100%;
        margin: 0 auto;
        margin-top: 30px;
        text-align: center;
    }

    .userdata{
        margin: 20px;
        text-align: center;
    }

    .stake_pad{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 20px;
        text-align: center;

    }

    .stake_pad input{
        padding: 6px 10px;
        margin: 8px 0;
        display: inline-block;
        border: 1px solid #ccc;
        border-radius: 4px;
        box-sizing: border-box;
    }
    
    .withdraw_pad{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 20px;
        text-align: center;
    }

    .withdraw_pad input{
        padding: 6px 10px;
        margin: 8px 0;
        display: inline-block;
        border: 1px solid #ccc;
        border-radius: 4px;
        box-sizing: border-box;
    }

    .approve_btn, .stake_btn, .withdraw_btn, .harvest_btn, .go_back{
        align-items: center;
        appearance: button;
        background-color: #0276FF;
        border-radius: 8px;
        border-style: none;
        box-shadow: rgba(255, 255, 255, 0.26) 0 1px 2px inset;
        box-sizing: border-box;
        color: #fff;
        cursor: pointer;
        display: flex;
        flex-direction: row;
        flex-shrink: 0;
        font-family: "RM Neue",sans-serif;
        font-size: 100%;
        line-height: 1.15;
        margin: 0;
        margin-top: 20px;
        padding: 10px 21px;
        text-align: center;
        text-transform: none;
        /* transition: color .13s ease-in-out,background .13s ease-in-out,opacity .13s ease-in-out,box-shadow .13s ease-in-out; */
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;
    }

    .go_back{
        max-width: 120px;
        text-align: center;
    }

    .dataloading{
        margin-top: 30px;
    }

    .connect_text{
        margin-top: 20px;
        font-weight: 700;
    }

    
}

